.question{
    padding-top: 96px;
    padding-bottom: 160px;
}
@media(max-width:992px){
    .question{
        padding-top: 60px;
        padding-bottom: 96px;
    }
}
@media(max-width:767px){
    .question{
        padding-top: 28px;
        padding-bottom: 56px;
    }
}
.question{
    h2{
        font-size: 48px;
        border-left: 8px solid #9F1213;
        padding-left: 16px;
        margin-bottom: 60px;
    }
    h3{
        font-size: 32px;
        margin-bottom: 36px;
    }
}
@media(max-width:992px){
    .question{
        h2{
            border-left: 4px solid #9F1213;
            margin-bottom: 40px;
        }
        h3{
            margin-bottom: 32px;
        }
    }
}
@media(max-width:767px){
    .question{
        h2{
            font-size: 28px;
            border-left: 4px solid #9F1213;
            padding-left: 8px;
            margin-bottom: 24px;
        }
        h3{
            font-size: 20px;
            margin-bottom: 24px;
        }
    }
}
.qa-list{
    h4{
        font-size: 20px; 
        margin-bottom: 16px;    
        font-weight: bold;
    }
}
@media(max-width:767px){
    .qa-list{
        h4{
            font-size: 16px;
            margin-bottom: 8px;    
        }
    }
}

