 .header {
    h1{
      a{
        background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true);
        background-repeat: no-repeat;
        height: 49px;
        text-indent: 101%;
        white-space: nowrap;
        overflow: hidden;
        display: block;
      }
    }
    background-color: #AA0601;
    display: flex;
    padding: 0 56px;
    justify-content: space-between; 
    .menu{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      li{
          text-align: center;
          &:last-child{
            margin-right: 0;
          }
      }
      a{
        display: block;
        padding: 38px 32px 41px 32px;
        &:hover{
          background: orange;
        }
      }
    }
    position: sticky;
    top: 0;
    z-index: 1;
}
@media(max-width:992px){
  .header{
    padding: 0px 39px;
    h1{
      a{
       text-align: center;
      }
    }
    .menu{
      a{
        padding-left: 20px;
        padding-right: 20px;

      }
    }
}
@media(max-width:767px){
    .header{
      flex-direction: column;
      padding: 0;
      .menu{
        a{
          padding-left: 32px;
          padding-right: 32px;
        }
      }
      .menu {
        a{
          padding-top: 0;
          padding-bottom: 0;
        }
        li{
          text-align: center;
          width: 50%;
          margin:0 auto 0 auto;
          border: 1px solid #fff;
          padding: 8px 0;
        }
      }
      // a{
      //   padding: 10px 0;
      // }
    }
}
}